	module.exports = {"data":{"markdownRemark":{"html":"<p>サイト名から内容が分かりにくいとか発音が分からないとかよく言われるので、<a href=\"http://www.sakura.ne.jp/\">さくら</a>に移転ついでにサイト名を “Force.Y” と変更。さらに初めて独自ドメイン (yasim.net) を使用。ちなみにもっと分かりにくくなったのはご愛嬌。末期には再び infoseek isweb に移転。手動ながらもページ構成はかなりブログに近いものになっていった。  <img src=\"https://danmaq.com/wp-content/uploads/1999/09/FY3.jpg\"> 70～100PV/day。背景色薄青紫(後期はくすんだ緑)。 この時も <a href=\"/tag/evil-kingdom\">MMORPG 製作計画</a>とか、かなり無茶染みたことやってた。(当時は“<a href=\"http://www.ragnarokonline.jp/\">ラグナロクオンライン</a>”に猿のようにハマってた)とりあえず目の前のタスクが片付いたらまた手を付けてみようかとは思っているが。 そう言えば、東方を初めて知ったのもこの頃。これまた猿のようにハマったが、まだ当時は二次創作を作ろうとか、ましてやサークル立ち上げようなんて思っても見なかった。</p>","frontmatter":{"cover":null,"date":"2002-08-08T00:00:00+09:00","strDate":"2002/8/7","title":"Force.Y"}}},"pathContext":{"path":"/ja/blog/2002/08/08-force-y/","langKey":"ja"}}

